@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@100;300;400;500;700;900&display=swap);
html,
body {
  background: rgb(0, 92, 184);
  background: linear-gradient(
      135deg,
      rgb(0, 92, 184) 0%,
      rgba(0, 45, 98, 1) 50%,
      rgba(0, 13, 28, 1) 100%
    )
    fixed;
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 400;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin: 0;
  padding: 0;
}

.App {
  color: #fff;
  text-align: center;
}

.Home {
  text-align: left;
}

.UkanaHeader,
.UkanaHeaderJp {
  display: inline-block;
  font-size: 5rem;
  font-weight: 500;
  margin: 0;
  margin-left: 1rem;
}

.UkanaHeaderJp {
  color: #bf1919;
  font-weight: 300;
}

.Subheader {
  margin: 0;
  margin-left: 1rem;
  font-style: italic;
}

.SubheaderStart {
  font-weight: 500;
  margin-left: 1rem;
}

.Navbar {
  background: #090645;
  padding: 0;
  text-align: left;
  width: auto;
}

a {
  color: #fff;
  margin: 0;
  padding: 0;
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  display: inline-block;
  list-style-type: none;
  margin-right: 1vw;
  text-decoration: none;
}

.ukanaLogo {
  -webkit-animation: logoHoverAfter 0.5s forwards;
          animation: logoHoverAfter 0.5s forwards;
  border: 3px solid transparent;
  border-radius: 12px;
  margin: 1vw 0 0.4vw 1vw;
  width: 1.9rem;
}

.ukanaLogo:hover {
  -webkit-animation: logoHover 0.5s forwards;
          animation: logoHover 0.5s forwards;
  border: 3px solid #cdc618;
  border-radius: 12px;
}

@-webkit-keyframes logoHover {
  0% {
    border-color: transparent transparent transparent transparent;
  }
  25% {
    border-color: #cdc618 transparent transparent transparent;
  }
  50% {
    border-color: #cdc618 #cdc618 transparent transparent;
  }
  75% {
    border-color: #cdc618 #cdc618 #cdc618 transparent;
  }
  100% {
    border-color: #cdc618 #cdc618 #cdc618 #cdc618;
  }
}

@keyframes logoHover {
  0% {
    border-color: transparent transparent transparent transparent;
  }
  25% {
    border-color: #cdc618 transparent transparent transparent;
  }
  50% {
    border-color: #cdc618 #cdc618 transparent transparent;
  }
  75% {
    border-color: #cdc618 #cdc618 #cdc618 transparent;
  }
  100% {
    border-color: #cdc618 #cdc618 #cdc618 #cdc618;
  }
}

@-webkit-keyframes logoHoverAfter {
  0% {
    border-color: #cdc618 #cdc618 #cdc618 #cdc618;
  }
  25% {
    border-color: #cdc618 #cdc618 #cdc618 transparent;
  }
  50% {
    border-color: #cdc618 #cdc618 transparent transparent;
  }
  75% {
    border-color: #cdc618 transparent transparent transparent;
  }
  100% {
    border-color: transparent transparent transparent transparent;
  }
}

@keyframes logoHoverAfter {
  0% {
    border-color: #cdc618 #cdc618 #cdc618 #cdc618;
  }
  25% {
    border-color: #cdc618 #cdc618 #cdc618 transparent;
  }
  50% {
    border-color: #cdc618 #cdc618 transparent transparent;
  }
  75% {
    border-color: #cdc618 transparent transparent transparent;
  }
  100% {
    border-color: transparent transparent transparent transparent;
  }
}

.navLink {
  margin: 0;
}

.non-home {
  position: relative;
  bottom: 1rem;
  font-size: 1.5vw;
}

.gradedKanji {
  color: #ae9898;
  font-style: italic;
}

.worded {
  -webkit-animation: wordedHoverAfter 0.5s forwards;
          animation: wordedHoverAfter 0.5s forwards;
  border: solid 0px;
  border-radius: 25px;
  color: #fff;
  outline-offset: 3px;
  outline-style: solid;
  outline-width: 3px;
  outline-color: transparent;
}

.worded:hover {
  -webkit-animation: wordedHover 0.5s forwards;
          animation: wordedHover 0.5s forwards;
}

@-webkit-keyframes wordedHover {
  from {
    background: #090645;
    color: #fff;
    outline-color: transparent;
  }
  to {
    background: #2a50a0;
    color: #cdc618;
    outline-color: #cdc618;
  }
}

@keyframes wordedHover {
  from {
    background: #090645;
    color: #fff;
    outline-color: transparent;
  }
  to {
    background: #2a50a0;
    color: #cdc618;
    outline-color: #cdc618;
  }
}

@-webkit-keyframes wordedHoverAfter {
  from {
    background: #2a50a0;
    color: #cdc618;
    outline-color: #cdc618;
  }
  to {
    background: #090645;
    color: #fff;
    outline-color: transparent;
  }
}

@keyframes wordedHoverAfter {
  from {
    background: #2a50a0;
    color: #cdc618;
    outline-color: #cdc618;
  }
  to {
    background: #090645;
    color: #fff;
    outline-color: transparent;
  }
}

.numbered {
  -webkit-animation: numberHoverAfter 0.5s forwards;
          animation: numberHoverAfter 0.5s forwards;
  border: solid 3px #fff;
  border-radius: 50%;
  height: 2.5vw;
  text-align: center;
  width: 2.5vw;
}

.numbered:hover {
  -webkit-animation: numberHover 0.5s forwards;
          animation: numberHover 0.5s forwards;
}

@-webkit-keyframes numberHover {
  0% {
    border-color: #fff #fff #fff #fff;
    background: #090645;
    color: #fff;
  }
  25% {
    border-color: #cdc618 #fff #fff #fff;
  }
  50% {
    border-color: #cdc618 #cdc618 #fff #fff;
  }
  75% {
    border-color: #cdc618 #cdc618 #cdc618 #fff;
  }
  100% {
    background: #2a50a0;
    border-color: #cdc618 #cdc618 #cdc618 #cdc618;
    color: #cdc618;
  }
}

@keyframes numberHover {
  0% {
    border-color: #fff #fff #fff #fff;
    background: #090645;
    color: #fff;
  }
  25% {
    border-color: #cdc618 #fff #fff #fff;
  }
  50% {
    border-color: #cdc618 #cdc618 #fff #fff;
  }
  75% {
    border-color: #cdc618 #cdc618 #cdc618 #fff;
  }
  100% {
    background: #2a50a0;
    border-color: #cdc618 #cdc618 #cdc618 #cdc618;
    color: #cdc618;
  }
}

@-webkit-keyframes numberHoverAfter {
  0% {
    background: #2a50a0;
    border-color: #cdc618 #cdc618 #cdc618 #cdc618;
    color: #cdc618;
  }
  25% {
    border-color: #cdc618 #cdc618 #cdc618 #fff;
  }
  50% {
    border-color: #cdc618 #cdc618 #fff #fff;
  }
  75% {
    border-color: #cdc618 #fff #fff #fff;
  }
  100% {
    border-color: #fff #fff #fff #fff;
    background: #090645;
    color: #fff;
  }
}

@keyframes numberHoverAfter {
  0% {
    background: #2a50a0;
    border-color: #cdc618 #cdc618 #cdc618 #cdc618;
    color: #cdc618;
  }
  25% {
    border-color: #cdc618 #cdc618 #cdc618 #fff;
  }
  50% {
    border-color: #cdc618 #cdc618 #fff #fff;
  }
  75% {
    border-color: #cdc618 #fff #fff #fff;
  }
  100% {
    border-color: #fff #fff #fff #fff;
    background: #090645;
    color: #fff;
  }
}

.wordSearchInput {
  -webkit-animation: inputFocusAfter 0.5s forwards;
          animation: inputFocusAfter 0.5s forwards;
  background-color: #142b65;
  border: solid 3px transparent;
  border-radius: 25px;
  color: #fff;
  font-family: "Noto Sans JP", sans-serif;
  height: 1.5rem;
  text-align: center;
  outline-offset: 3px;
  outline-style: solid;
  outline-width: 3px;
  outline-color: transparent;
  width: 12vw;
}

::-webkit-input-placeholder {
  color: #cdc618;
  font-size: 1.15vw;
}

:-ms-input-placeholder {
  color: #cdc618;
  font-size: 1.15vw;
}

::placeholder {
  color: #cdc618;
  font-size: 1.15vw;
}

.wordSearchInput:focus {
  -webkit-animation: inputFocus 0.5s forwards;
          animation: inputFocus 0.5s forwards;
  background: #fff;
  outline-color: #cdc618;
  color: #000;
}

.wordSearchInput:focus::-webkit-input-placeholder {
  color: #000;
}

.wordSearchInput:focus:-ms-input-placeholder {
  color: #000;
}

.wordSearchInput:focus::placeholder {
  color: #000;
}

@-webkit-keyframes inputFocus {
  from {
    background: #142b65;
    color: #fff;
    outline-color: transparent;
  }
  to {
    background: #fff;
    color: #000;
    outline-color: #cdc618;
  }
}

@keyframes inputFocus {
  from {
    background: #142b65;
    color: #fff;
    outline-color: transparent;
  }
  to {
    background: #fff;
    color: #000;
    outline-color: #cdc618;
  }
}

@-webkit-keyframes inputFocusAfter {
  from {
    background: #fff;
    color: #000;
    outline-color: #cdc618;
  }
  to {
    background: #142b65;
    color: #fff;
    outline-color: transparent;
    text-align: right;
  }
}

@keyframes inputFocusAfter {
  from {
    background: #fff;
    color: #000;
    outline-color: #cdc618;
  }
  to {
    background: #142b65;
    color: #fff;
    outline-color: transparent;
    text-align: right;
  }
}

.KanjiCard {
  background: #fff;
  border-radius: 20px;
  color: #000;
  margin: auto;
  margin-top: 1rem;
  padding: 0.5rem;
  max-width: 100vw;
  text-align: left;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.KanjiCardHeader {
  text-align: center;
}

.KanjiCardKanji {
  font-size: 5rem;
  margin: 0;
}

.KanjiCardMeaning {
  margin: 0;
}

.refreshRandom {
  background-color: #bf1919;
  border: solid 3px #bf1919;
  border-radius: 25px;
  color: #fff;
  cursor: pointer;
  font-size: 2rem;
  margin: 0 auto;
  max-width: 100vw;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.KanjiMiniCard {
  background: #fff;
  border-radius: 20px;
  color: #000;
  display: inline-block;
  margin: 0 0.5rem;
  margin-top: 1rem;
  padding: 0.5rem;
  text-align: left;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.KanjiCardHeader {
  text-align: center;
}

.KanjiMiniCardKanji {
  font-size: 3rem;
  margin: 0;
}

.Hiragana,
.Katakana {
  display: flex;
  flex-flow: row wrap;
}

.KanaMiniCard {
  background: #fff;
  border-radius: 20px;
  color: #000;
  margin: 0 1%;
  margin-top: 1rem;
  padding: 0.5rem;
  text-align: center;
  flex: 1 1 16%;
}

.emptyCard {
  background: rgba(255, 255, 255, 0);
  border-radius: 20px;
  color: #000;
  margin: 0 1%;
  margin-top: 1rem;
  padding: 0.5rem;
  text-align: center;
  flex: 0 0 16%;
}

.KanaMiniCardKana,
.KanaMiniCardRomaji {
  font-size: 3.5vw;
  margin: 0;
  padding: 0;
}

@media screen and (max-width: 798px) and (min-width: 600px) {
  .KanaMiniCard {
    flex: 1 1 14%;
  }
}

@media screen and (max-width: 599px) {
  .KanaMiniCard {
    flex: 1 1 12%;
  }
}

.GenkiBar {
  background: #5b5959;
  padding: 0.1rem 0 0.05rem;
  position: relative;
  width: 100vw;
  z-index: 1;
}

.GenkiLinksItem {
  display: inline-block;
  list-style-type: none;
  text-decoration: none;
}

.GenkiItem {
  font-size: 1rem;
  margin-left: 2rem;
  -webkit-animation: adminitemRev 0.5s forwards;
          animation: adminitemRev 0.5s forwards;
}

.GenkiItem:hover {
  -webkit-animation: adminitem 0.5s forwards;
          animation: adminitem 0.5s forwards;
}

@-webkit-keyframes adminitem {
  from {
    background: #3f3d3d;
    border-radius: 12px;
  }
  to {
    background: #476098;
    border-radius: 12px;
  }
}

@keyframes adminitem {
  from {
    background: #3f3d3d;
    border-radius: 12px;
  }
  to {
    background: #476098;
    border-radius: 12px;
  }
}

@-webkit-keyframes adminitemRev {
  from {
    background: #476098;
    border-radius: 12px;
  }
  to {
    background: #5b5959;
    border-radius: 12px;
  }
}

@keyframes adminitemRev {
  from {
    background: #476098;
    border-radius: 12px;
  }
  to {
    background: #5b5959;
    border-radius: 12px;
  }
}

.GenkiChapter {
  background: #5b5959;
  padding: 0.1rem 0 0.05rem;
  position: relative;
  top: 1rem;
}

